.cantacts {
    height: 100vh;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container {
    width: 700px;
    height: 500px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.myCantact {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: rgb(116, 197, 252);
}
.form {
    display: flex;
    flex-direction: column;
    width: 60%;
    border: 1px solid;
}
.send {
    background-color: black;
    color: white;
}

input {
    height: 20px;
}
